'use client'
import { useEffect, useState } from "react"
import { toast } from "react-hot-toast"
import Image from "next/image"
import Loading from "@/components/Loading"
import { useAuth, useUser } from "@clerk/nextjs"
import axios from "axios"
import { Trash2, Edit, Check, X, AlertTriangle } from "lucide-react"

export default function StoreManageProducts() {
    const { getToken } = useAuth()
    const { user } = useUser()
    const currency = process.env.NEXT_PUBLIC_CURRENCY_SYMBOL || '$'
    
    const [loading, setLoading] = useState(true)
    const [products, setProducts] = useState([])
    const [editingId, setEditingId] = useState(null)
    const [editForm, setEditForm] = useState({})

    const fetchProducts = async () => {
        try {
             const token = await getToken()
             const { data } = await axios.get('/api/store/product', {headers: { Authorization: `Bearer ${token}` } })
             setProducts(data.products.sort((a, b)=> new Date(b.createdAt) - new Date(a.createdAt)))
        } catch (error) {
            toast.error(error?.response?.data?.error || "Failed to load products")
        }
        setLoading(false)
    }

    const deleteProduct = async (id) => {
        if(!confirm("Are you sure you want to permanently delete this product?")) return;
        try {
            const token = await getToken()
            await axios.delete(`/api/store/product?id=${id}`, {headers: { Authorization: `Bearer ${token}` }})
            setProducts(prev => prev.filter(p => p.id !== id))
            toast.success("Product deleted successfully")
        } catch (error) {
            toast.error("Failed to delete product")
        }
    }

    const startEdit = (product) => {
        setEditingId(product.id)
        setEditForm({ ...product })
    }

    const saveEdit = async () => {
        try {
            const token = await getToken()
            await axios.put('/api/store/product', editForm, {headers: { Authorization: `Bearer ${token}` }})
            
            setProducts(prev => prev.map(p => p.id === editingId ? editForm : p))
            setEditingId(null)
            toast.success("Product updated successfully")
        } catch (error) {
            toast.error("Failed to update product")
        }
    }

    const toggleStock = async (productId) => {
        try {
            const token = await getToken()
            const { data } = await axios.post('/api/store/stock-toggle',{ productId }, {headers: { Authorization: `Bearer ${token}` } })
            setProducts(prev => prev.map(p => p.id === productId ? {...p, inStock: !p.inStock} : p))
            toast.success(data.message)
        } catch (error) {
            toast.error("Error updating status")
        }
    }

    useEffect(() => { if(user) fetchProducts() }, [user])

    if (loading) return <Loading />

    return (
        <div className="mb-20">
            <h1 className="text-2xl text-slate-500 mb-6">Manage <span className="text-slate-800 font-medium">Products</span></h1>
            
            {/* Low Stock Alert Banner */}
            {products.some(p => p.stock < 10) && (
                <div className="mb-6 p-4 bg-orange-50 text-orange-800 rounded-lg border border-orange-200 flex items-center gap-3">
                    <AlertTriangle size={20} />
                    <p><strong>Low Stock Warning:</strong> Some products have less than 10 items in stock.</p>
                </div>
            )}

            <div className="overflow-x-auto rounded-lg border border-gray-200 shadow-sm">
                <table className="w-full text-left text-sm text-slate-600">
                    <thead className="bg-slate-50 text-slate-700 uppercase font-semibold">
                        <tr>
                            <th className="px-6 py-4">Product Name</th>
                            <th className="px-6 py-4">Price</th>
                            <th className="px-6 py-4">Stock (Qty)</th>
                            <th className="px-6 py-4 text-center">Status</th>
                            <th className="px-6 py-4 text-center">Actions</th>
                        </tr>
                    </thead>
                    <tbody className="divide-y divide-slate-100">
                        {products.map((product) => (
                            <tr key={product.id} className="hover:bg-slate-50 transition-colors">
                                <td className="px-6 py-4">
                                    <div className="flex gap-4 items-center">
                                        <Image width={48} height={48} className='rounded-md object-cover border border-slate-200' src={product.images[0]} alt="" />
                                        {editingId === product.id ? (
                                            <input className="border border-slate-300 rounded px-3 py-1.5 w-40 focus:ring-2 focus:ring-green-500 outline-none" value={editForm.name} onChange={e => setEditForm({...editForm, name: e.target.value})} />
                                        ) : (
                                            <span className="font-medium text-slate-800">{product.name}</span>
                                        )}
                                    </div>
                                </td>
                                <td className="px-6 py-4 font-medium">
                                    {editingId === product.id ? (
                                        <input type="number" className="border border-slate-300 rounded px-2 py-1 w-24" value={editForm.price} onChange={e => setEditForm({...editForm, price: Number(e.target.value)})} />
                                    ) : (
                                        `${currency}${product.price}`
                                    )}
                                </td>
                                <td className="px-6 py-4">
                                    {editingId === product.id ? (
                                        <input type="number" className="border border-slate-300 rounded px-2 py-1 w-24" value={editForm.stock} onChange={e => setEditForm({...editForm, stock: Number(e.target.value)})} />
                                    ) : (
                                        <span className={`font-medium ${product.stock < 10 ? 'text-orange-600' : 'text-slate-600'}`}>
                                            {product.stock} units
                                        </span>
                                    )}
                                </td>
                                <td className="px-6 py-4 text-center">
                                    <label className="relative inline-flex items-center cursor-pointer">
                                        <input type="checkbox" className="sr-only peer" checked={product.inStock} onChange={() => toggleStock(product.id)} />
                                        <div className="w-11 h-6 bg-slate-200 peer-focus:outline-none rounded-full peer peer-checked:after:translate-x-full peer-checked:after:border-white after:content-[''] after:absolute after:top-[2px] after:left-[2px] after:bg-white after:border-gray-300 after:border after:rounded-full after:h-5 after:w-5 after:transition-all peer-checked:bg-green-600"></div>
                                    </label>
                                </td>
                                <td className="px-6 py-4">
                                    <div className="flex items-center justify-center gap-2">
                                        {editingId === product.id ? (
                                            <>
                                                <button onClick={saveEdit} className="text-green-600 hover:bg-green-50 p-2 rounded-full transition"><Check size={18}/></button>
                                                <button onClick={() => setEditingId(null)} className="text-slate-500 hover:bg-slate-100 p-2 rounded-full transition"><X size={18}/></button>
                                            </>
                                        ) : (
                                            <>
                                                <button onClick={() => startEdit(product)} className="text-blue-600 hover:bg-blue-50 p-2 rounded-full transition"><Edit size={18}/></button>
                                                <button onClick={() => deleteProduct(product.id)} className="text-red-500 hover:bg-red-50 p-2 rounded-full transition"><Trash2 size={18}/></button>
                                            </>
                                        )}
                                    </div>
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
        </div>
    )
}